@import '../../../utils/variables/colors';

.instructionsSection {
	padding: 64px 16px;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-between;
	header {
		font-size: 3rem;
		border-top: 1px solid $color-fifth;
		border-bottom: 1px solid $color-fifth;
		padding: 16px;
		letter-spacing: 1px;
		margin-bottom: 32px;
	}
	p {
		margin-bottom: 32px;
		font-size: 1.2rem;
	}
}
