@import '../../../utils/variables/colors';

.ingredientsSection {
	padding: 32px 16px;
	margin-top: 64px;
	background-color: $color-third;
	display: flex;
	align-items: center;
	justify-content: space-between;
	flex-direction: column;
	header {
		font-size: 3rem;
		display: block;
		margin-bottom: 32px;
		padding: 0 16px 16px 16px;
		letter-spacing: 1px;
		border-bottom: 1px solid $color-fifth;
	}
	ul {
		display: block;
		li {
			font-size: 1.5rem;
			list-style: none;
		}
	}
}
